export const findDesigner = path => designer.find(i => i.path.split('/').slice(-1).toString() === path)

const listDesigner = () => {
  const tmp =  designer.sort((a, b) => {
    if(a.name.toLowerCase() > b.name.toLowerCase()) {
      return 1
    }
    else {
      return -1
    }
  })
  return tmp
}

const listDesignerByName = Object.values(listDesigner().reduce((acc, cur) => {
  const duplicate = designer.filter(d => cur.name.toLowerCase().slice(0,1) === d.name.toLowerCase().slice(0,1))
  return {...acc, [cur.name.toLowerCase().slice(0, 1)] : duplicate }
}, {}))

const designersData = {
  listDesigner,
  findDesigner,
  listDesignerByName,
}


export default designersData
